export default{
	methods:{
		onLoad(){
			var that = this;
			//1 取出订单缓存数据 放到data里面
			this.orderInfo = this.$Util.getCache("current_order_info");
			//2 取出当前店铺放到头部店铺信息
			this.currentShop = this.$Util.getCache('current_shop');
			//取出订单数据hashKey
			this.orderInfo.map(v => {
				this.hashKey.unshift(v.hashKey)
			})
			

			this.options = [{
				title:"微信支付",
				note:"推荐使用微信支付",
				icon:"icon-weixinzhifu text-success",
				value:"wxpay"
			}]
			this.payMethod = 'wxpay'

		},
		/**
		 * 拉起微信支付
		 */
		pullWeiXinPay(res){
			var that = this
			// 跳转到支付页面
			uni.login({
				success: (e) => {
					this.$H.get('/mannerdish/pay/payment?order_id='+res.order_id+'&code='+e.code,{},{
						token:true,
						native:true
					})
					.then(res=>{
						// 发起小程序支付
						/*
						{
							appId:"wxb1f305de99c0fe32",
							nonceStr:"ZM7mqDUe5wYVpz1h",
							package:"prepay_id=wx31215725257777f851bd31661280875100",
							paySign:"2114A262F841E9A8080972A0D5C6E7F7",
							signType:"MD5",
							timeStamp:"1577800644"
						}
						*/
					   let paymentData = res.data.data;
					   uni.requestPayment({
					       timeStamp: paymentData.timeStamp,
					       nonceStr: paymentData.nonceStr,
					       package: paymentData.package,
					       signType: 'MD5',
					       paySign: paymentData.paySign,
					       success: (res) => {
					           // 重定向/防止重复支付
					           uni.switchTab({
					           	// url: '../mannerorder/ordertoday?order_id='+that.$data.order_id
								url: '/pages/mannerorder/ordertoday'
					           });
					           console.log('success:' + JSON.stringify(res));
					       },
					       fail: (res) => { //支付失败
							   this.callDelayCancel(); 
					           uni.showModal({
					               content: "支付失败,原因为: " + res
					                   .errMsg,
					               showCancel: false
					           })
					       },
					       complete: () => {
					           this.loading = false;
					       }
					   })
					})
					.catch(err=>{ //支付失败
						this.loading = false  
						uni.showToast({
							title: '支付失败',
							icon: 'none'
						});
						
						//跳转到订单页面
						uni.switchTab({
							// url: '../mannerorder/ordertoday?order_id='+that.$data.order_id
							url: '/pages/mannerorder/ordertoday'
						});
					
					})
				},
				fail: (e) => {
					console.log("fail", e);
					//用户取消的时候我们进行一个定时消单操作这个时候要请求后台
					this.callDelayCancel(); 
					this.loading = false;
					uni.showModal({
					    content: "支付失败,原因为: " + e.errMsg,
					    showCancel: false
					})
				}
			})
		},
		
		// 下单，支付
		openPayMethods(){
			var that = this;
			// 防止重复下单
			if(this.loading) return;
			uni.showLoading({
				title:"加载中"
			})
			
			uni.show
			let options = {
				hashKey:this.hashKey.join(','),
				flag:'cartPay', //表示从购物车进行支付
				currentShopName:this.currentShop.name,
				currentShopId:this.currentShop.id
			}
			// 是否选择优惠券
			if(this.coupon.id > 0){
				options.coupon_user_id = this.coupon.id
			}
			//在这里加一把锁
			this.loading = true
			//如果是第一次支付
			if(!this.order_id || !this.order_sn )
			{
				this.$H.post('/mannerdish/order/orderSubmit',options,{
					token:true
				}).then(res=>{
					that.$data.order_id = res.order_id
					that.$data.order_sn = res.order_id
					that.$data.order_result = res
					this.loading = false
					// // 通知购物车更新数据
					// uni.$emit('updateCart')
					return res;
				}).then(res=>{  //创建订单后拉取支付
					this.pullWeiXinPay(res)
				}).catch(err=>{
					this.loading = false
					console.log(err);
					uni.showToast({
						title: '创建订单失败',
						icon: 'none'
					});
				})
			}else{ //如果已经生成订单直接拉取支付
				this.pullWeiXinPay(this.$data.order_result)
			}
			
		},
	
	    //请求后台进行订单延迟取消
		callDelayCancel(){
			//拼接退单数据
			let data = {
				'order_id' : this.order_result.order_id,
				'order_sn' : this.order_result.order_sn
			}
			//请求退单接口
			this.$H.post('/mannerdish/order/orderDelayCancel',data,{
				token:true
			}).then(res=>{
				console.log(res+"测试")
			}).catch(err=>{
				this.loading = false
				console.log(err);
				uni.showToast({
					title: '取消订单失败',
					icon: 'none'
				});
			})
		}
	},	

	computed: {
		// 显示价格
		totalPrice(){
			let price = 0;
			for(let i = 0;i<this.orderInfo.length;i++)
			{
			    price += this.orderInfo[i].num * this.orderInfo[i].showPrice*100
			}
			
			//规格价格
			return price/100;
			//下面可能会加上浓缩份数的价格
		},
	},
	data(){
		return{
			loading:false,
			//订单缓存数据
			orderInfo:[],
			//该订单的总价
			total_price:45,
			currentShop:{},
			couponCount:0,
			coupon:{
				id:0,
				type:0,
				value:0
			},
			order_id:0,
			order_sn:"",
			hashKey:[],
			payMethod:"alipay",
			options:[],
			order_result:{}
		}
	},
}
